<template>
  <header class="timeline-header">
    <h3 v-if="subtitle">{{subtitle}}</h3>
    <h4>{{title}}</h4>
    <timeline-current-date v-if="currentDatePosition" :currentDatePosition="currentDatePosition"></timeline-current-date>
  </header>
</template>

<style>
  @import "variables.css";

  .timeline-header {
    padding:2rem 0 0 2rem;
    position: relative;
  }

  .timeline-header:before {
    background: #fff;
    content: '';
    height: 1rem;
    width: 1rem;
    border: .2rem solid #4a90e2;
    display: block;
    border-radius: 50%;
    position: absolute;
    top: 1rem;
    left: 0rem;
    z-index: 1
  }

  .timeline-header h3 {
    color: #5e6066;
    font-size: 1rem;
    font-weight: normal;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0;
    left: 2rem;
    white-space: nowrap;
  }

  .timeline-header h4 {
    font-size: 1.3rem;
    font-weight: normal;
    padding: 0;
    margin: 0;
    white-space: nowrap;
  }

  .event-separator__past .timeline-header:before {
    border-color: #62c6f1;
  }
  .event-separator__past .timeline-header h4 {
    color: #5E6066;
  }

  .event-separator__current .timeline-header:before {
    background: #fff;
  }
  .event-separator__current .timeline-header:after {
    background: #e73e42;
    content: '';
    height: .75rem;
    width: .75rem;
    display: block;
    border-radius: 50%;
    position: absolute;
    top: 1.3rem;
    left: 0.3rem;
    z-index: 1
  }
</style>

<script>
  import fecha from 'fecha';
  import weekNumber from '../helpers/week-number'
  import TimelineCurrentDate from './TimelineCurrentDate'

  export default {
    props: ['date', 'currentDatePosition'],
    components: {
      TimelineCurrentDate
    },
    computed: {
      locale () {
        return this.$translate && this.$translate.locale && this.$translate.locale.fecha
      },
      title () {
        return this.getSectionTitle(this.$store.state.timelineMode, this.date, this.locale);
      },
      subtitle () {
        return this.getSectionSubtitle(this.$store.state.timelineMode, this.date, this.locale);
      }
    },
    methods: {
      getSectionTitle(mode, date, i18n) {
        date = new Date(date);
        switch (mode){
          case 'year':
            return fecha.format(date, 'YYYY'); // Year
          case 'month':
            return fecha.format(date, 'MMMM', i18n); // Month
          case 'week':
            return weekNumber(date) + ' ' + this.t('Week'); // Week 10
          default:
            return fecha.format(date, 'MMMM Do', i18n); // September, 26
        }
      },
      getSectionSubtitle (mode, date, i18n) {
        date = new Date(date);
        switch (mode){
          case 'year':
            return false; // Nothing here
          case 'month':
            return fecha.format(date, 'YYYY'); // Year
          case 'week':
            return fecha.format(date, 'MMMM', i18n); // September
          default:
            return weekNumber(date) + ' ' + this.t('Week');  // Week 10
        }
      }
    }
  }
</script>



