<template>
  <main class="timeline" v-bind:class="{ 'compact-mode': classCompactMode }">
    <component v-for="event in events" v-bind:is="currentView(event.type)" v-bind:event="event.data"></component>

    <!-- timeline-section v-bind:section="section" v-for="section in sections"></timeline-section -->
  </main>
</template>

<style>
  @import "variables.css";

  .timeline {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
    height:calc(100vh);
    padding: var(--timelineTopPadding) 0 0 2rem;
    box-sizing: border-box;
    overflow-x: scroll;
    overflow-y: hidden;
    background: linear-gradient(to bottom, #4a90e2 0%, #4a90e2 100%);
    background-size: 3px 3px;
    background-repeat: repeat-x;
    background-position: 0  3rem;
    margin-left: 20rem;
  }

  @media (--header-small) {
    .timeline {
      height: calc(100vh - var(--headerHeightSm));
    }
  }

  /* Vertical layout for timeline */
  @media (--timeline-small) {
    .timeline {
      display: block;
      padding: 2rem 0 2em 4.2rem;
      overflow-x: hidden;
      overflow-y: auto;
      background-repeat: repeat-y;
      background-position: 2rem 0;
    }
    .timeline {
      margin-left: 0;
    }

  }
</style>

<script>
  import TimelineEvent from './TimelineEvent'
  import TimelineSeparator from './TimelineSeparator'
  import { mapState } from 'vuex'
  import _ from 'lodash';
  import fecha from 'fecha';
  import weekNumber from '../helpers/week-number'

  function createSection(type, data) {
    return {
      type,
      data: Object.assign({}, data)
    }
  }

  function createSeparator(date, prevDate, isPast, isCurrent, currentDatePosition){
    return createSection('separator', {
      date: date,
      prevDate: prevDate || false
    })
  }

  function createEvent(data){
    return createSection('event', data)
  }

  function getSectionKey(mode, date) {
    date = new Date(date);
    switch (mode){
      case 'year':
        return fecha.format(date, 'YYYY');
      case 'month':
        return fecha.format(date, 'YYYYMM');
      case 'week':
        const weekNum = weekNumber(date);
        return fecha.format(date, 'YYYY') + '' + (weekNum < 10 ? '0'+weekNum : weekNum);
      default:
        return fecha.format(date, 'YYYYMMDD');
    }
  }

  export default {
    components: {
      TimelineEvent,
      TimelineSeparator
    },
    methods: {
      currentView: function(type){
        return type === 'separator' ? 'timeline-separator' : 'timeline-event'
      }
    },
    computed: {
      ...mapState({
        classCompactMode: "displayModeCompact",
        allEvents (state) {
          return _.sortBy(state.events, 'startDate')
        },
        filteredEvents (state){
          const date = new Date(state.route.params.date).getTime();
          const byFilter = _.pickBy(state.eventsFilter, _.isNumber);
//          const byDate = function( v ) {
//            return v.startDate >= date;
//          };

          return _.chain(this.allEvents).filter(byFilter).value();
        },
        events (state){
          let previousEvent;
          return _.reduce(this.filteredEvents, function (result, value) {
              if (!previousEvent) {
                result.push(createSeparator(value.startDate));
              }
              if (previousEvent) {
                var prevKey = getSectionKey(state.timelineMode, previousEvent.startDate);
                var currentKey = getSectionKey(state.timelineMode, value.startDate);
                if (prevKey !== currentKey) {
                  result.push(createSeparator(value.startDate, previousEvent.startDate));
                }
              }
              result.push(createEvent(value));
              previousEvent = value;
              return result;
            }, [])
        }
      })
    }
  }
</script>



