<template>
  <button class="button_settigns__calendar-mode" @click="switchMode()"><span>{{buttonSwitchModeTitle}}</span></button>
</template>

<style>
  .button_settigns__calendar-mode {
    background: none !important;
    border: 0 !important;
    color: #fff !important;
    font-size: 12px !important;
    text-align: left !important;
    padding:0 !important;
    height:auto !important;
    text-transform: none !important;
    font-weight: normal !important;
    text-decoration: none !important;
    width: auto;
    padding-right: .3rem !important;
    padding-top: .2rem !important;
    float: right;
  }
  .button_settigns__calendar-mode span {
    border-bottom: 1px dashed #fff;
  }
</style>

<script>
  export default {
    methods: {
      switchMode: function() {
        this.$store.dispatch('switchMode')
      }
    },
    computed: {
      compactMode () {
        return this.t('Compact Mode')
      },
      normalMode () {
        return this.t('Normal Mode')
      },
      buttonSwitchModeTitle: function(){
        return this.$store.state.displayModeCompact === true ? this.normalMode : this.compactMode
      }
    }
  }
</script>



