<template>
  <footer>
    <section>
      <router-link v-if="loginAsEditor" :to="{ name: 'addEvent', params: routeParams}"><i class="icon icon-plus-squared"></i></router-link>
      <router-link :to="{ name: 'login', params: routeParams}">
        <i v-if="login" class="icon icon__logout icon-user"></i>
        <i v-if="!login" class="icon icon__login icon-login"></i>
      </router-link>
    </section>
  </footer>
</template>

<style>
  @import "variables.css";

  footer {
    display: block;
    box-sizing: border-box;
    text-align: left;
    z-index: 999;
    margin-top: 2rem;
    margin-bottom: 1rem;
  }

  footer > section {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: flex-start;
    align-items: flex-start;
  }

  footer > section > a {
    display: inline-block;
    order: 0;
    flex: 0 1 auto;
    align-self: auto;
  }

  footer .icon {
    display: inline-block;
    color: #16407d;
    background: #fff;
    z-index: 905;
    border: 1px solid #16407d;
    font-size: 1.5rem;
    padding: .2rem;
    cursor: pointer;
  }

  @media (--timeline-small) {
    footer .icon {
      font-size:2rem;
    }
  }
</style>

<script>
  import { mapState } from 'vuex'
  import FooterCalendarButton from './FooterCalendarButton'

  export default {
    components: {
      FooterCalendarButton
    },
    computed: {
      ...mapState({
        routeParams (state){
          return state.route.params;
        },
        loginAsEditor(state) {
          return !!state.user && state.user.authorities && state.user.authorities.indexOf('ROLE_ADMIN') > -1;
        },
        login(state) {
          return !!state.user;
        }
      })
    }

  }
</script>



