<template>
  <span class="current-date" v-bind:style="currentDateStyle"><b>{{currentDate}}</b></span>
</template>

<style>
  @import "variables.css";

  .current-date {
    background: #e73e42;
    content: '';
    height: .5rem;
    width: .5rem;
    border: .1rem solid #fff;
    margin-top:2px;
    display: block;
    border-radius: 50%;
    position: absolute;
    top: 1.1rem;
    left: 0.3rem;
    z-index: 1;
  }

  .current-date b {
    font-weight: normal;
    font-size: 11px;
    color: #e73e42;
    position: absolute;
    width: 10rem;
    top:.5rem;
  }

  @media (--timeline-small) {
    .current-date b {
      top: -0.6rem;
      left: 1.2rem;
    }
  }
</style>

<script>
  import fecha from 'fecha';
  import throttle from 'lodash/throttle'
  import weekNumber from '../helpers/week-number'

  function getPositionSide(htmlEl) {
    return (htmlEl < 800) ? 'top' : 'left'
  }

  export default {
    props: ['currentDatePosition'],
    data: function () {
      return {
        positionSide: getPositionSide(window.innerWidth)
      }
    },
    mounted: function () {
      const handlerResize = (e) => {
        this.positionSide = getPositionSide(e.currentTarget.innerWidth)
      }

      this.handlerResizeThrottled = throttle(handlerResize, 300, {
        leading: false,
        trailing: true
      });

      window.addEventListener('resize', this.handlerResizeThrottled);
    },
    beforeDestroy: function () {
      window.removeEventListener('resize', this.handlerResizeThrottled);
    },
    computed: {
      currentDateStyle () {
        let elStyle = {};
        elStyle[this.positionSide] = this.currentDatePosition;
        return elStyle;
      },
      currentDate () {
        return fecha.format(this.$store.state.currentDate, 'MMM DD');
      }
    }
  }
</script>



