<script>
  import Vue from 'vue'
  import store from '../store'
  import _ from 'lodash'

  function getThemeOfCategory(categoryId) {
    var category = _.find(store.state.categories, {id: categoryId});
    if (!category) {
      return false;
    }
    if (!category.theme && category.parentId != category.id) {
      return getThemeOfCategory(category.parentId);
    }
    return category.theme || false;
  }

  export default Vue.directive('ThemeByCategory', function (el, binding) {
    if (binding.value === binding.oldValue) {
      return;
    }
    const theme = getThemeOfCategory(binding.value);
    if (!theme) {
      return;
    }
    var regex = new RegExp("(?:^|\s)" + theme + "(?!\S)");
    if (!el.className.match(regex)) {
      el.className += " " + theme;
    }
  })
</script>



