<template>
  <span class="calendar_day" v-bind:class="{ prevmonth: event.prevClass, nextmonth: event.nextClass, currentday: event.currentClass, activeday: event.activeClass, event: event.events }">
    <router-link :to="{ name: 'date', params: Object.assign({}, {date: event.date}) }"  v-if="event.events">{{ event.day }}</router-link>
    <span v-else>{{ event.day }}</span>
  </span>
</template>


<script>
  export default {
    props: ['event']
  }
</script>

<style>
  .calendar_day {
    position: relative;
    border: 1px solid transparent;
  }

  .calendar_day > a,
  .calendar_day > span {
    text-decoration: none;
    width: 100%;
    text-align: center;
    display: inline-block;
    line-height: 1;
    box-sizing: border-box;
    color: #666;
    font-size: 11px;
    padding: 0.4rem 0;
    margin-bottom: .2rem;

  }
  .calendar_day > a {
     color: #16407d;
  }


  .calendar_day.prevmonth,
  .calendar_day.nextmonth {
    & > a, & > span {
      color:#ddd;
    }
  }
  .calendar_day.currentday { /* Today */
    & > a, & > span {
      color: blue;
    }
  }
  .calendar_day.activeday {
    border-color:#e73e42 !important;
  }

  .calendar_day.event:after {
    background: #16407d;
    border-radius: 50%;
    bottom: .1rem;
    display: block;
    content: '';
    width: .5rem;
    height: .5rem;
    left: 50%;
    top:50%;
    margin: .4em 0 0 -.2rem;
    position: absolute;
  }

  .calendar_day.event:hover {
    border: 1px solid #16407d
  }
  .calendar_day.event.prevmonth:hover, .calendar_day.event.nextmonth:hover {
    border: 1px solid transparent
  }

  .calendar_day.event.nextmonth:after,
  .calendar_day.event.prevmonth:after{
    background:  #ddd;
  }
  .calendar_day.event.currentday:after {
    background: blue
  }

</style>



