<template>
  <div class="event-separator" v-bind:class="{'event-separator__past': isPast, 'event-separator__current': isCurrent}">
    <div class="event-separator__header">
      <timeline-section-header v-bind:date="event.date" v-bind:currentDatePosition="currentDatePosition"></timeline-section-header>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  .event-separator {
    margin: 0;
    padding: 0 0 0 1rem;
    width:1px;
  }

  .compact-mode .event-separator {
    padding: 0 0 0 2rem;
  }

  .event-separator:first-child {
    padding: 0 !important;
  }

  @media (--timeline-large) {
    .event-separator {
      height:calc(100vh - var(--headerHeightLg));
    }
    .event-separator__header {
      margin-top: -5.5rem;
    }
  }

  @media (--header-medium) {
    .event-separator {
      height:calc(100vh - var(--headerHeightMd));
    }
  }

  @media (--timeline-small) {
    .event-separator {
      height:auto;
      width:100%;
      margin: 1rem 0 1rem -2.7rem;
      padding: 0 !important;
    }
  }

</style>

<script>
  import fecha from 'fecha';
  import weekNumber from '../helpers/week-number'
  import TimelineSectionHeader from '../components/TimelineSectionHeader'

  function isCurrentDate(currentDate, date, mode) {
    date = new Date(date);
    currentDate = new Date(currentDate);
    switch (mode){
      case 'year':
        return fecha.format(date, 'YYYY') === fecha.format(currentDate, 'YYYY');
      case 'month':
        return fecha.format(date, 'YYYYMM') === fecha.format(currentDate, 'YYYYMM');
      case 'week':
        const weekNum = weekNumber(date);
        const currentWeekNum = weekNumber(currentDate);
        return (weekNum === currentWeekNum) && fecha.format(date, 'YYYY') === fecha.format(currentDate, 'YYYY')
      default:
        return fecha.format(date, 'YYYYMMDD') === fecha.format(currentDate, 'YYYYMMDD');
    }
  }

  function getCurrentDotPosition(date, dateLeft, dateRight) {
    return (date - dateLeft)/(dateRight - dateLeft) - 1;
  }

  export default {
    props: ['event'],
    components: {
      TimelineSectionHeader
    },
    computed: {
      isPast () {
        return !this.isCurrent && this.event.date < this.$store.state.currentDate;
      },
      isInPeriod () {
        return !this.isCurrent && this.event.date > this.$store.state.currentDate && this.event.prevDate < this.$store.state.currentDate;
      },
      isCurrent () {
        // this.$store.state.timelineMode
        return isCurrentDate(this.$store.state.currentDate, this.event.date, this.$store.state.timelineMode)
      },
      currentDatePosition () {
        if(!this.isInPeriod) {
          return false;
        }
        const position = (19*getCurrentDotPosition(this.$store.state.currentDate, this.event.prevDate, this.event.date)).toFixed(2);
        return (position > -3 ? -3 : position ) + 'rem'
      }
//      periodWidth () {
//        return this.event.prevDate ? { marginLeft: getPeriodWidth(this.$store.state.timelineMode, this.event.date - this.event.prevDate) + 'px' } : {};
//      }
    }
  }
</script>




