<template>
  <div class="modal-mask" v-bind:class="[ modalClass ]">
    <div class="modal-wrapper">
      <div class="modal-container" @click.stop>
        <div class="modal_header">
          <div class="close modal_button__close" @click="close()">✕</div>
          <slot name="header"></slot>
        </div>
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  .modal-mask {
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .modal-container {
    width: 100%;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
    position: relative;
    text-align: left;
  }
  .modal_content {
    height:calc(100vh - 7rem);
  }
  .modal_content__scroller {
    overflow-y: scroll;
  }

  @media (--timeline-large) {
    .modal-container {
      max-width: 75rem;
      height: 56rem;
    }
    .modal_content {
      height: 49rem;
    }
  }

  .modal_header  {
    color: #fff;
    padding: 3rem 2.5rem 1rem;
    background: #ed4044;
    box-sizing: border-box;
    height: 7rem;
  }

  .modal_header h3 {
    color: #fff;
    padding: 0;
    margin: 0;
    font: 400 2.4rem/1.2 var(--baseHeaderFontFamily);
    text-transform: uppercase
  }

  .modal_button__close {
    position: absolute;
    z-index: 10000;
    top: 0;
    right: 0;
    line-height: 2.5rem;
    width: 2.5rem;
    cursor: pointer;
    text-align: center;
    font-family: Arial, sans-serif;
    background: #fff;
    background: rgba(255, 255, 255, 0.3);
    color: #000;
  }

</style>

<script>
  export default {
    props: ['onClose', 'modalClass'],
    methods: {
      close: function () {
        this.onClose();
      }
    },
    mounted: function () {
      document.addEventListener("keydown", (e) => {
        if (this.show && e.keyCode == 27) {
          this.onClose();
        }
      });
    }
  }
</script>



