<template>
  <div class="location-selector">
    <a v-if="isMarkersExist" v-on:click="removeMarker" class="button location-selector_remove-marker">
      <span v-translate>Select location</span>
    </a>
    <div class="gmap" id="gmap"></div>
  </div>
</template>

<script>
  import _ from 'lodash/core';
  import GMaps from 'gmaps'
  import GoogleMapsLoader from '../helpers/GoogleMaps'

  export default {
    props: ['value'],
    data: function () {
      return {
        map: undefined,
        google: undefined,
        gmapSettings: {
          div: '#gmap',
          zoom: this.value.zoom || 8,
          lat: this.value.latitude || 59.89444803635239,
          lng: this.value.longitude || 10.79183578491211,
          scrollwheel: false,
          panControl: false,
          mapTypeControl: false,
          overviewMapControl: false,
          streetViewControl: false
        }
      }
    },
    mounted () {
      GoogleMapsLoader.load((google) => {
        this.google = google;

        this.map = new GMaps(this.gmapSettings);

        this.map.on("rightclick", (event) => {
          this.emitChange(event.latLng.lat(), event.latLng.lng(), this.map.getZoom());
        });

        this.map.on("zoom_changed", () => {
          this.emitChange(this.latitude, this.longitude, this.map.getZoom());
          this.map.setCenter(this.latitude, this.longitude);
        });

        if (this.value.latitude && this.value.longitude) {
          this.addMarker(this.value.latitude, this.value.longitude)
        }
      });
    },
    beforeDestroy () {
      if (!this.google) {
        return;
      }
      this.google.maps.event.clearInstanceListeners(window);
      this.google.maps.event.clearInstanceListeners(document);
      this.google.maps.event.clearInstanceListeners(this.$el.getElementsByClassName('gmap')[0]);
      this.removeMarkers();
      this.map = undefined;
      this.google = undefined;
    },
    computed: {
      isMarkersExist () {
        return this.map && this.map.markers ? this.map.markers.length > 0 : false
      },
      marker () {
        return [this.value.latitude, this.value.longitude];
      }
    },
    watch: {
      marker (data) {
        this.addMarker.apply(this, data);
      }
    },
    methods: {
      addMarker (lat, lng) {
        if (!this.map) {
          return;
        }
        this.removeMarkers();
        if (lat && lng) {
          this.map.addMarker({
            lat: lat,
            lng: lng,
          });
          this.map.setCenter(lat, lng);
        }
      },
      removeMarkers () {
        if (!this.map) {
          return;
        }
        _.each(this.map.markers, function (marker) {
          marker.setMap(null);
        });
        this.map.markers = [];
      },
      removeMarker () {
        this.emitChange('', '', this.map.getZoom());
      },
      emitChange (latitude, longitude, zoom) {
        this.$emit('input', {latitude, longitude, zoom});
      }
    }

  }
</script>

<style>
  .gmap {
    height: 250px;
    margin-bottom: 1rem;
  }

  .location-selector {
    position: relative;
  }

  .location-selector_remove-marker {
    color: red;
    float: right;
    height: 2.4rem;
    line-height: 2.6rem;
    padding: 0 1rem;
    margin: -2rem 0 0 0;
    width: auto;
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 2px;
    letter-spacing: .1rem;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: bold;
    font-size: 1.1rem;
    z-index: 2;
    position: absolute;
    right: 1rem;
    top: 3rem;
  }

  .location-selector_remove-marker:hover {
    border-color: #888;
  }

</style>



