<template>
  <div class="footer-buttons">
    <button-edit-event v-if="loginAsEditor" v-bind:id="id" v-bind:klass="'footer-button_edit'"><i class="icon-pencil"></i><span v-translate>Edit Event</span></button-edit-event>
    <button-remove-event v-if="loginAsEditor" v-bind:id="id" v-bind:klass="'footer-button_delete'"><i class="icon-cancel"></i><span v-translate>Delete Event</span></button-remove-event>
    <!--<button @click="alert" class="footer-button_print"><i class="icon-print"></i><span>Utskrift</span></button>-->

    <event-social-buttons v-bind:id="id"></event-social-buttons>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  import ButtonEditEvent from './ButtonEditEvent';
  import ButtonRemoveEvent from './ButtonRemoveEvent';
  import EventSocialButtons from './EventSocialButtons';

  export default {
    props: ['id', 'login'],
    components: {
      ButtonEditEvent,
      ButtonRemoveEvent,
      EventSocialButtons
    },
    computed: {
      ...mapState({
        loginAsEditor(state) {
          return !!state.user && state.user.authorities && state.user.authorities.indexOf('ROLE_ADMIN') > -1;
        }
      })
    }
  }
</script>

<style>
  @import "variables.css";

  .footer-button_print,
  .footer-button_edit,
  .footer-button_delete {
    background: #ed4044;
    border: 1px solid #fff;
    display: block;
    height: auto;
    margin: 0 0 0.8rem;
    padding: 0.3rem 1rem 0.4rem;
    width: 100%;
    text-align: left;
    color: #fff;
    font: 700 1.6rem/1.2 var(--baseHeaderFontFamily);
    text-transform: uppercase;

    & i {
      float: right;
    }
  }

  .footer-button_print:hover {
    color: #000;
  }

  .footer-button_edit {
    color: #FFF;
    background-color: #33C3F0;
    border-color: #33C3F0;
  }
  .footer-button_edit:hover {
    background-color: #1EAEDB;
    border-color: #1EAEDB;
  }

  .footer-button_delete {
    background-color: #fefefe;
    background: none;
    border-color: #666;
    color: #666;
  }
  .footer-button_delete:hover {
    border-color: #000;
    color: #000;
  }

  @media (--phone) {
    .footer-button_print, .footer-button_edit, .footer-button_delete {
      font-size: 1.3rem;
      padding: 1rem .5rem 1rem 1rem;
    }
  }

</style>



