<template>
  <div class="calendar-wrapper">
    <div class="calendar">
      <header class="calendar_header">
        <h2>{{monthTitle}} <span>{{currentYear}}</span></h2>
        <button @click="goPrevious" class="btn-prev fontawesome-angle-left"><i class="fa fa-chevron-left"></i>◀
        </button>
        <button @click="goNext" class="btn-next fontawesome-angle-right"><i class="fa fa-chevron-right"></i>▶</button>
      </header>
      <main>
        <div class="calendar_titles clearfix">
          <b v-for="value in daysLabels"><i>{{value}}</i></b>
        </div>
        <div class="calendar_days clearfix">
          <calendar-td v-for="value in calendarData" v-bind:event="value"></calendar-td>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
  import fecha from 'fecha';
  import _ from 'lodash/core';
  import CalendarTd from './CalendarTd';

  export default {
    data: function () {
      return {
        daysLabels: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
        daysInMonth: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
        currentDate: new Date(this.$store.state.route.params.date),
        checked: false,

      }
    },
    components: {
      CalendarTd
    },
    computed: {
      currentDay: function () {
        return this.currentDate.getDay();
      },
      currentMonth: function () {
        return this.currentDate.getMonth();
      },
      currentYear: function () {
        return this.currentDate.getFullYear();
      },
      daysInCurrentMonth: function () {
        return this.daysInMonth[this.currentMonth]
      },
      daysInPreviousMonth: function () {
        return this.daysInMonth[this.currentMonth === 0 ? 11 : this.currentMonth - 1]
      },
      daysInNextMonth: function () {
        return this.daysInMonth[this.currentMonth === 11 ? 0 : this.currentMonth + 1]
      },
      firstDayInCurrentMonth: function () {
        return new Date(this.currentYear, this.currentMonth, 1).getDay();
      },
      lastDayInCurrentMonth: function () {
        return new Date(this.currentYear, this.currentMonth, this.daysInCurrentMonth).getDay();
      },
      monthTitle: function () {
        return fecha.format(this.currentDate, 'MMMM'); // Month
      },
      calendarData: function () {
        var start = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth(), -this.firstDayInCurrentMonth + 1);
        var end = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() + 1, 6 - this.lastDayInCurrentMonth);
        var calendarObj = [];

        while (start < end) {
          var newDate = start.setDate(start.getDate() + 1);
          var dateKey = fecha.format(start, 'YYYY-MM-DD');
          start = new Date(newDate - 1);

          calendarObj.push({
            day: fecha.format(start, 'D'),
            date: dateKey,
            prevClass: new Date(this.currentDate.getFullYear(), this.currentDate.getMonth()) > start,
            nextClass: new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() + 1) < start,
            activeClass: dateKey === this.$store.state.route.params.date,
            currentClass: dateKey === fecha.format(new Date(), 'YYYY-MM-DD'),
            events: this.events[dateKey] || false
          });
        }
        return calendarObj;
      },
      events (){
        return _.reduce(this.$store.state.flatEvents, function (result, value, key) {
          var key = fecha.format(new Date(parseInt(key)), 'YYYY-MM-DD');
          result[key] ? result[key] += value : result[key] = value;
          return result;
        }, {});
      },
      eventsRange: function () {
        return {
          start: +new Date(this.calendarData[0].date),
          end: +new Date(this.calendarData[this.calendarData.length - 1].date)
        }
      }
    },
    methods: {
      goPrevious: function () {
        this.currentDate = new Date(this.currentYear, this.currentMonth - 1, 1);
        this.$store.dispatch('loadFlatEvents', this.eventsRange);
      },
      goNext: function () {
        this.currentDate = new Date(this.currentYear, this.currentMonth + 1, 1);
        this.$store.dispatch('loadFlatEvents', this.eventsRange);
      }
    },
    mounted () {
      this.$store.dispatch('loadFlatEvents', this.eventsRange);
    }
  }
</script>

<style>
  @import "variables.css";

  .calendar-wrapper {
    text-align: center;
    border: none;
    margin: 0;
    background: #fff;
    overflow: hidden;
  }

  .calendar {
    text-align: center;
    overflow: hidden;
    padding: 0 0 0.2rem 0;
  }

  .calendar_header {
    position: relative
  }

  .calendar_header button {
    border: 0;
    background: #fff;
  }

  .calendar_header h2 {
    font-size: 1.7rem;
    margin-bottom: 0;
    margin-top: .5rem;
  }

  .calendar_header h2 span {
    font-weight: normal
  }

  .calendar_header .btn-prev, .calendar_header .btn-next {
    text-decoration: none;
    height: 2em;
    font-size: .75em;
    line-height: 2em;
    margin: -1em 0 0 0;
    padding: 0 1rem;
    position: absolute;
    top: 54%;
    width: 1em;
    color: #16407d;
    background: none;
  }

  .calendar_header .btn-prev:hover, .calendar_header .btn-next:hover {
    color: #000
  }

  .calendar_header .btn-prev {
    left: 0
  }

  .calendar_header .btn-next {
    right: 10px;
  }

  .calendar_titles > b {
    display: inline-block;
    font-weight: normal;
    text-transform: uppercase;
    color: #5e6066;
    border: 1px solid transparent;
    text-align: center;
    font-size: 9px;
    width: 13%;
  }

  .calendar_titles > b > i {
    font-style: normal;
  }

  .calendar_days {
    color: #7c8a95;
  }

  .calendar_days > span {
    display: inline-block;
    width: 13%;
    color: #000;
    text-align: center;
  }

  @media (--timeline-small) {
    .calendar-wrapper {
      margin: 2rem auto 0;
    }

    .calendar {
      border: 0;
      width: 100%;
      padding: 0 0 0.5rem 0;
    }

    .calendar_header h2 {
      font-size: 2.7rem;
    }

    .calendar_titles > b {
      width: 13.5%;
      font-size: 11px;
    }

    .calendar_day > a, .calendar_day > span {
      font-size: 14px !important;
      padding: 1.2rem 0 !important;
    }
  }
</style>



