import request from '../HttpRequest'
import {defaults, find, reject} from 'lodash'

function createNewEvent(args) {
  return defaults(args, {
    id: null,
    description: null,
    endDate: null,
    startDate: null,
    registrationDate: null,
    title: null,
    mapZoom: null,
    geoDescription: null,
    longitude: null,
    latitude: null,
    categoryId: null,
    levelId: null,
    organizationId: null,
    regionId: null,
    createdBy: null
  })
}

function EventClass() {
  let loadedEvents;

  function getEvents() {
    if (!loadedEvents) {
      loadedEvents = loadEvents();
      return loadedEvents;
    }

    return getCachedEvents();
  }

  function loadEvents() {
    return request.get('/events')
      .then(setCachedEvents)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function setCachedEvents(data) {
    loadedEvents = data;
    return getCachedEvents();
  }

  function getCachedEvents() {
    return Promise.resolve(loadedEvents);
  }

  function getEventById(id) {
    const event = find(loadedEvents, {id});

    if (event) {
      return Promise.resolve(event);
    }

    return request.get('/events/' + id)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function getNewEvent() {
    return Promise.resolve(createNewEvent({
      latitude: 59.9138688,
      longitude: 10.752245399999993,
      mapZoom: 12
    }));
  }

  function saveEvent(data) {
    if (data.id) {
      return request.put('/events/' + data.id, data)
        .then((data) => {
          loadedEvents = reject(loadedEvents, {id: data.id});
          loadedEvents.push(data);
          return data;
        })
        .catch((e) => {
          console.error(e);
          Promise.reject(e);
        });
    }

    return request.post('/events', data)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function removeEventById(id) {
    return request.delete('/events/' + id)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function getFlatEvents(data){
    return request.get('/events/' + 'range?from='+ data.start + '&to=' + data.end)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });

  }


  this.getAll = getEvents;

  this.saveEvent = saveEvent;
  this.removeEventById = removeEventById;

  this.getById = getEventById;

  this.getNewEvent = getNewEvent;

  this.getFlatEvents = getFlatEvents;

}

export default EventClass



