<template>
  <div>
    <textarea readonly v-if="!isRequired" v-model="value" hidden></textarea>
    <textarea readonly v-if="isRequired" v-model="value" v-validate.initial="value" data-rules="required|blank_html" required
            :class="{'has-error': errors.has('value') }" hidden></textarea>
    <div class="editor"></div>
  </div>
</template>

<script>
  import MediumEditor from 'medium-editor';
  require('medium-editor/dist/css/medium-editor.css');
  require('medium-editor/dist/css/themes/beagle.css');
  require('../assets/css/medium-editor.css');

  export default {
    props: ['value', 'required'],
    data () {
      return {
        editor: undefined
      }
    },
    methods: {
      setContentToEditor (data) {
        if(data && this.editor){
          this.editor.setContent(data);
          this.setContentToEditor = function(){};
        }
      }
    },
    updated () {
      this.setContentToEditor(this.value);
    },
    mounted () {
      this.editor = new MediumEditor(this.$el.getElementsByClassName("editor")[0], {
        disableDoubleReturn: true,
        disableExtraSpaces: true,
        autoLink: true,
        toolbar: {
          buttons: ['h2', 'h3', 'bold', 'italic', 'underline', 'anchor', 'orderedlist', 'unorderedlist', 'removeFormat'],
          static: false,
          /* options which only apply when static is true */
          align: 'left',
          sticky: true,
          updateOnEmptySelection: true
        },
        anchor: {
          customClassOptionText: 'Button', //translate
          placeholderText: 'Paste or type a link' //translate
        },
        placeholder: {
          text: ''
        },
        paste: {
          forcePlainText: true,
          cleanPastedHTML: false,
          cleanAttrs: ['class', 'style', 'dir'],
          cleanTags: ['meta', 'noscript', 'object', 'link', 'map', 'optgroup', 'option', 'progress', 'script', 'select', 'source', 'style', 'textarea', 'video', 'title'],
          unwrapTags: [ 'div', 'main', 'article', 'footer', 'header', 'kbd', 'code', 'keygen', 'label', 'legend', 'mark', 'nav', 'output', 'param', 'pre', 'q', 'rp', 'rt', 'ruby', 'samp', 'section', 'small', 'span', 'sub', 'summary', 'sup', 'table', 'tbody', 'td', 'tfoot', 'th', 'thead', 'time', 'tr', 'track', 'var', 'wbr']
        }
      });

      this.editor.subscribe('editableInput', () => {
        const content = this.editor.serialize()['element-0'].value;
        this.$emit('input', content);
      });
    },
    beforeDestroy () {
      this.editor.destroy();
    },
    computed: {
      isRequired () {
        return typeof this.required !== 'undefined'
      }
    }
  }
</script>



