<template>
  <div class="datepicker">
    <select v-model="day" name="day" :class="{'has-error': errors.has('day') }"
            v-validate.initial="day" data-rules="required|numeric" required>
      <option value="" v-translate>Day</option>
      <option v-for="day in days" v-bind:value="day">{{day}}</option>
    </select>
    <select v-model="month" name="month" :class="{'has-error': errors.has('month') }"
            v-validate.initial="month" data-rules="required|numeric" required>
      <option value="" v-translate>Month</option>
      <option v-for="(month, i) in months" v-bind:value="i">{{month}}</option>
    </select>
    <select v-model="year" name="year" :class="{'has-error': errors.has('year') }"
            v-validate.initial="year" data-rules="required|numeric" required>
      <option value="" v-translate>Year</option>
      <option v-for="year in years" v-bind:value="year">{{year}}</option>
    </select>
  </div>
</template>
<script>
  import {map, get} from 'lodash';
  import fecha from 'fecha';

  export default {
    props: ['value'],
    data () {
      const date = new Date();
      return {
        year: date.getFullYear(),
        month: date.getMonth(),
        day: date.getDate(),
        years: [...Array(20)].map((v, i) => {
          return 2015 + i;
        })
      }
    },
    computed: {
      months () {
        return [...Array(12)].map((v, i) => {
          if(this.$translate.locale && this.$translate.locale){
            return this.$translate.locale.fecha.monthNames[i];
          }
          return i+1;
        })
      },
      days () {
        if(this.year === '' || this.month === ''){
          return [];
        }
        const daysInMonth = new Date(this.year, this.month + 1, 0).getDate();
        return [...Array(daysInMonth)].map((v, i) => {
          return i + 1;
        })
      }
    },
    watch: {
      'value' (value, oldValue) {
        if (!value || value === oldValue) {
          return;
        }
        const date = new Date(value);
        this.year = date.getFullYear();
        this.month = date.getMonth();
        this.day = date.getDate();
      }
    },
    updated () {
      if(this.year === '' || this.month === '' || this.day === ''){
        return;
      }
      this.$emit('input', new Date(this.year, this.month, this.day).getTime());
    }
  }
</script>
<style>
  .datepicker {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    align-items: flex-start;
  }

  .datepicker select {
    order: 0;
    flex: 2 0 0;
    align-self: auto;
  }

  .datepicker select + select {
    margin-left: .5rem;
  }

  .datepicker select:nth-child(2) {
    flex: 4 0 0;
  }

</style>



