<template>
  <modal :modal-class="'login'" v-bind:class="{profile: isLoggedIn}" :on-close="close">
    <h3 slot="header" v-translate>{{headerTitle}}</h3>
    <form v-on:submit.prevent id="form-login" name="form-login" class="form">
      <div v-if="!isLoggedIn">
        <ul v-if="errors.errors.length" class="form_errors">
          <li v-for="error in errors.errors">{{ error.msg }}</li>
        </ul>
        <label v-translate>fieldLoginName</label>
        <input type="text" id="loginform" name="login" v-model="login" v-validate.initial="login" data-rules="required|min:3" :class="{'has-error': errors.has('login') }" required>

        <label v-translate>fieldPassword</label>
        <input type="password" name="password" v-model="password" v-validate.initial="password" data-rules="required|min:3" :class="{'input': true, 'has-error': errors.has('password') }" required>
      </div>

      <div v-if="isLoggedIn">
        <div class="user-profile clearfix">
          <i class="user-profile_avatar icon icon-user"></i>

          <h4 class="user-profile_name" v-if="$store.state.user.username">
            <span v-translate>Hello</span>, {{$store.state.user.username}}!
          </h4>

          <div class="user-profile_email" v-if="$store.state.user.email">{{$store.state.user.email}}</div>
        </div>
      </div>
      <button v-on:click.stop.prevent="submitAction" type="submit" v-bind:disabled="errors.errors.length > 0" class="button-primary">{{buttonTitle}}</button>
    </form>

  </modal>
</template>


<style>
  @import "variables.css";

  .login .modal-container {
    max-width: 30rem;
    min-height: 23rem;
    height: auto;
  }

  .login .modal--header {
    background: #16407d;
  }

  .user-profile {
    border-bottom:1px dotted #ccc;
    margin-bottom:2rem;
    padding-bottom:1rem;
  }

  .user-profile_avatar {
    float: left;
    display: block;
    width: 5rem;
    height: 5rem;
    line-height:4.7rem;
    font-size: 3rem;
    text-align: center;
    padding: 0;
    border: 1px solid #ccc;
    border-radius: 50%;
    margin-right: 1.5rem;
    color:#999;
    background: #f1f1f1;
  }

  .user-profile_name {
    margin: 0;
    padding-top: 1.5rem;
  }
  .user-profile_email {
    color: #999;
    font-size:1.3rem;
    margin-bottom:2rem;
  }

  .user-settings {
    border-bottom:1px dotted #ccc;
    margin-bottom:2rem;
  }

</style>


<script>
  import Modal from './Modal'
  import router from '../router/index'
  import notification from '../helpers/Notification'

  export default {
    data: function() {
      return {
        login: '',
        password: ''
      }
    },
    components: {
      Modal
    },
    computed: {
      isLoggedIn () {
        return !!this.$store.state.user
      },
      headerTitle () {
        return this.isLoggedIn ? 'Profile' : 'Login'
      },
      buttonTitle () {
        return this.isLoggedIn ? 'Log out' : 'Log in'
      }
    },
    methods: {
      close: function(){
        router.push({name:'date', params: this.$route.params})
      },
      submitAction: function(e) {
        this.$validator.validateAll();

        if (this.errors.any()) {
          e.preventDefault();
          return;
        }

        if(this.isLoggedIn){
          this.$store.dispatch('logoutUser');
          notification.info(this.t('LoggedOut'));
          return;
        }

        //if everything is OK
        this.$store.dispatch('loginUserAndReturnPromise', {
          login: this.login,
          password: this.password
        }).then(()=>{
          notification.success(this.t('LoggedIn'));
        }).catch((e)=>{
          notification.error(e.message || 'Undefined error on server.');
          this.password = '';
        });

      }
    },
    notifications: {
      showToster: {}
    }
  }
</script>



