import request from '../HttpRequest'
import _ from 'lodash';

function OrganizationClass() {
  let loadedOrganizations;

  function getOrganizations() {
    if (!loadedOrganizations) {
      loadedOrganizations = loadOrganizations();
      return loadedOrganizations;
    }

    return getCachedOrganizations();
  }

  function loadOrganizations() {
    return request.get('/organizations')
      .then(setCachedOrganizations)
      .catch((e) => {
        console.error(e);
        Promise.reject(e);
      });
  }

  function setCachedOrganizations(data) {
    loadedOrganizations = data;
    return getCachedOrganizations();
  }

  function getCachedOrganizations() {
    return Promise.resolve(loadedOrganizations);
  }

  this.getAll = getOrganizations;

  this.getById = function (id) {
    return getOrganizations().then(function (data) {
      return _.find(data, {id: id});
    });
  };
}

export default OrganizationClass



