<template>
  <router-link class="event-item" :to="{ name: 'viewEvent', params: Object.assign({}, routeParams, {id: event.id}) }">
    <div @mouseover="showOverlay = true" @mouseleave="showOverlay = false">
      <div class="event-box" v-theme-by-category="event.categoryId">
        <div class="event-box--img">
          <organization-image v-bind:organizationId="event.organizationId"></organization-image>
        </div>
        <div class="event-box--type">
          <category-name v-bind:categoryId="event.categoryId"></category-name>
        </div>
      </div>
      <timeline-event-info v-bind:event="event" v-bind:showOverlay="showOverlay"></timeline-event-info>
    </div>

  </router-link>
</template>

<script>
  import OrganizationImage from './OrganizationImage';
  import CategoryName from './CategoryName';
  import TimelineEventInfo from './TimelineEventInfo';
  import ThemeByCategory from '../directives/ThemeByCategory';
  import { mapState } from 'vuex';


  export default {
    components: {
      OrganizationImage,
      CategoryName,
      TimelineEventInfo
    },
    directives: {
      ThemeByCategory
    },
    data: function(value) {
      return {
        showOverlay : false
      }
    },
    props: ['event'],
    computed: {
      ...mapState({
        routeParams (state){
          return state.route.params;
        }
      })
    }
  }
</script>

<style>
  @import "variables.css";

  .event-item {
    margin: 0 1rem 1.5rem;
    width: 16rem;
    text-decoration: none;
    position: relative;
  }

  .event-item:hover {
    box-shadow: 0 0 5px rgba(0,0,0,.5);
  }

  .event-box {
    display: table;
    table-layout: fixed;
    height: 4.2rem;
    width: 16rem;
    margin-bottom: .5rem;
    cursor: pointer;
  }

  .event-box.lowercase span {
    text-transform: none;
    font-weight: 500
  }

  .event-box.blue-red .event-box--img {
    background: #16407d;
    border: 0
  }

  .event-box.blue-red .event-box--type {
    background: #e73e42;
    color: #fff;
    border: 0
  }

  .event-box--img {
    display: table-cell;
    width: 4rem;
    position: relative;
    background: #9b9b9b;
    z-index: 1;
    border: 2px solid #9b9b9b;
  }

  .event-box--img img {
    max-height: 100%;
    max-width: 100%;
    width: auto;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto
  }

  .event-box--type {
    display: table-cell;
    vertical-align: middle;
    text-transform: uppercase;
    color: #16407d;
    font-size: 1.1rem;
    font-weight: bold;
    border: 2px solid #9b9b9b;
    border-left: 0;
  }

  .event-box--type > div {
    padding: 0 .7rem;
    display: block
  }

  .compact-mode .event-item {
    margin-bottom: 2rem;
    width: 6rem;
    display: inline-block;
    vertical-align: top;
  }

  .compact-mode .event-item .event-box {
    width: 6rem;
  }

  .compact-mode .event-box--type {
    display: none !important;
  }

  /* Vertical layout for timeline */
  @media (--timeline-large) {
    .event-item {
      flex: 0 1 auto;
      align-self: auto;
      order: 0;
    }
  }
  @media (--timeline-small) {
    .event-item {
      margin: 0 2rem 2rem 0rem;
      display: inline-block;
      vertical-align: top;
    }
  }

  @media (--timeline-small) {
    .compact-mode .event-item:last-child {
      margin-bottom: 4rem;
    }
  }
</style>




