<template>
  <form class="filter-list" action="">
    <event-filter-item
      v-bind:values="filterOrganizations"
      v-bind:callback="dispatchFilterBy('switchFilterByOrganization')"
      v-bind:value="filterOrganizationValue"><span v-translate>Organisasjoner</span></event-filter-item>
    <event-filter-item
      v-bind:values="filterLevels"
      v-bind:callback="dispatchFilterBy('switchFilterByLevel')"
      v-bind:value="filterLevelValue"><span v-translate>Nivå</span></event-filter-item>
    <event-filter-item
      v-bind:values="filterRegions"
      v-bind:callback="dispatchFilterBy('switchFilterByRegion')"
      v-bind:value="filterRegionValue"><span v-translate>Regions</span></event-filter-item>
    <event-filter-item
      v-for="category in filterCategories"
      v-bind:values="category.options"
      v-bind:callback="dispatchFilterBy('switchFilterByCategory')"
      v-bind:value="filterCategoryValue"><span v-translate>{{category.name}}</span></event-filter-item>
    <!-- button>Apply</button -->
  </form>
</template>

<script>
  import _ from 'lodash';
  import EventFilterItem from './EventFilterItem';

  function getOptions(data) {
    return _.map(data, (v => _.pick(v, ['id', 'name'])))
  }

  export default {
    components: {
      EventFilterItem
    },
    computed: {
      filterOrganizations () {
        return getOptions(this.$store.state.organizations)
      },
      filterOrganizationValue () {
        return this.$store.state.eventsFilter.organizationId || ''
      },
      filterLevels () {
        return getOptions(this.$store.state.levels)
      },
      filterLevelValue () {
        return this.$store.state.eventsFilter.levelId || ''
      },
      filterRegions () {
        return getOptions(this.$store.state.regions)
      },
      filterRegionValue () {
        return this.$store.state.eventsFilter.regionId || ''
      },
      filterCategories () {
        return this.$store.getters.categoriesTree;
      },
      filterCategoryValue () {
        return this.$store.state.eventsFilter.categoryId || ''
      }
    },
    methods: {
      dispatchFilterBy (action) {
        return function (data) {
          return this.$store.dispatch(action, data);
        }
      }
    }
  }
</script>

<style>
  @import "variables.css";

  .filter-list {
    margin-right: 5px;
    font-size: 0;
    box-sizing: border-box;
    font-family: var(--baseHeaderFontFamily);
    float: none;
    margin-top: .3rem;
  }

  .filter-list button {
    display: none
  }
</style>



